import Vue from 'vue'

import Repository2 from "../Repository2";
import MutationType2 from "../MutationType2";

const state = {
  ds:[
    //{id:1,ten:'thuc 101'},
    {id:1,ten:'Quần Jean',don_gia:1000,cat_id:1},
    {id:2,ten:'Quần Tây',don_gia:1000,cat_id:1},
    {id:3,ten:'Quần Kaki',don_gia:1000,cat_id:1},
    {id:4,ten:'Áo sơ mi',don_gia:1000,cat_id:2},
  ],
};
const getters = {
  lds: state => state.ds,//Product/lds
  getById: (state) => (id) => {//Product/getById
    //how to use
    //return this.$store.getters['Product/getById'](id); 
    let index = state.ds.findIndex(r => r.id == id)
    return state.ds[index];
  },
  getByCatId:(state)=>(id)=>{//this.$store.getters.getByLop(x.id);//<----- Remember!!!
    //how to use
    //return this.$store.getters['Product/getByCatId'](catId); 
    return state.ds.filter( r => r.cat_id == id )
  },



};
const mutations = {
  set_ds(state, v) {
    state.ds = v;
  },
  add(state, r) {//this.$store.commit('add',r)
    state.ds.push(r);
  },
  del(state, id) {//this.$store.commit('del',id)
    let index = state.ds.findIndex(r => r.id == id)
    state.ds.splice(index, 1);
  },
  update(state, r) {//this.$store.commit('update',r)
    let index = state.ds.findIndex(row => row.id == r.id)
    //state.ds[index]=r;//<-----------------Thằng Vue không cho phép
    Vue.set(state.ds, index, r);
  },
};

const actions = {
  lds( {commit} ){
    return new Promise((resolve, reject) => {
      Repository2.post("/api_public/farm_list/lds", {})
      .then(res => {
        commit('set_ds', res.data.msg);
        resolve(res);
      })
      .catch(err => {
        reject(err);
      });
    })
  },
  add({commit},r){
    return new Promise((resolve, reject) => {
      Repository2.post('/api_public/farm_list/add',r)
      .then(res=>{
        commit('add',res.data.msg);
        resolve(res);
      })
      .catch(err => {
        reject(err);
      });
    })
  },

  del({commit},id){
    return new Promise((resolve, reject) => {
      Repository2.post('/api_public/farm_list/del',{id:id})
      .then(res=>{
        commit('del',id);
        resolve(res);
      })
      .catch(err => {
        reject(err);
      });
    })
  },

  update({commit},rec){

    return new Promise((resolve, reject) => {
      Repository2.post('/api_public/farm_list/update',{id:rec.id,r:rec.r})
      .then(res=>{
        if(res.data.kq=='ok'){
          let r=rec.r;
          r.id=rec.id;
          commit('update',r);
          resolve(res);
        }else{
          reject(res);
        }
      })
      .catch(err => {
        reject(err);
      });
    })

  },

};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
};

